var server = window.location.hostname;
var port = "1337";
var Graph = /** @class */ (function () {
    function Graph(vector, id_graph) {
        this.vector = vector;
        this.id_graph = id_graph;
    }
    return Graph;
}());
var add_button = document.getElementById('add');
var create_button = document.getElementById('create_vector');
var target = document.getElementById('target');
var id_graph = document.getElementById("id_graph");
var status_request = document.getElementById("status_request");
function getValueAndConvertToNumber() {
    var vector = [];
    for (var _i = 0, _a = Array.prototype.slice.call(target.getElementsByTagName("input")); _i < _a.length; _i++) {
        var item = _a[_i];
        vector.push(Number(item.value));
    }
    return vector;
}
function addElement() {
    target.insertAdjacentHTML('beforeend', "<input id=" + target.getElementsByTagName('input').length + ">");
}
function createVector() {
    var vector = getValueAndConvertToNumber();
    var obj = { vector: vector };
    fetch("http://" + server + ":" + port + "/graph/", { method: "POST", headers: {
            'Accept': 'application/json, text/plain',
            'Content-Type': 'application/json;charset=UTF-8'
        }, body: JSON.stringify(obj) })
        .then(function (response) {
        console.log(response);
        return response.json().then(function (data) { return ({ status: response.status, body: data }); });
    }).then(function (data) {
        if (data.status == 200) {
            id_graph.innerHTML = data.body.id;
        }
        else if (data.status == 404) {
            status_request.innerHTML = "Not Found";
        }
    });
}
add_button.addEventListener('click', addElement);
create_button.addEventListener('click', createVector);
